.header{background-color:#fff;border-bottom:1px solid #eaeaea;padding:10px;position:fixed;top:0;left:0;right:0;z-index:100}.header .logo img{width:50px;height:50px;object-fit:contain}.header .content{display:flex;align-items:center;justify-content:space-between;width:100%}.header .nav{display:flex;align-items:center;margin-left:1rem}.header .nav a{color:#4a4a4a;margin:0 .5rem;text-decoration:none;font-weight:500}.header .nav a:hover{color:#0070f3}.header .search{display:flex;align-items:center;margin-left:auto}.header .search .searchBtn{padding:10px 20px}.header .search input{padding:10px 20px;border:1px solid #ccc;border-radius:4px 0 0 4px;font-size:1rem}.header .search input::placeholder{color:#999}.header .search button{background-color:#fff;border:1px solid #ccc;border-left:none;padding:.3rem .5rem;border-radius:0 4px 4px 0;cursor:pointer;display:flex;align-items:center;justify-content:center;font-size:1rem}.header .search button:hover{background-color:#f0f0f0}.header .language{display:flex;align-items:center;margin-left:1rem}.header .language button{background:none;border:1px solid #ccc;padding:.3rem .5rem;margin-right:.5rem;border-radius:4px;cursor:pointer}.header .language button:last-child{margin-right:0}.header .language button:hover{background-color:#f0f0f0}.header .auth{margin-left:1rem}.header .auth button{background-color:#4a0070;color:#fff;border:none;padding:.3rem 1rem;border-radius:4px;cursor:pointer}.header .auth button:hover{background-color:#600094}